.footer-container {
    width: 100%;
    padding: 30px 16px;
  }
  
  .footer-line {
    border-top: 1px solid white;
  }
  
  .footer_content-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 24px;
    margin-top: 16px;
  }
  
  .footer_social_media-container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
  
  .footer-socials {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    border: 2px solid white;
    margin: 0 12px;
  }
  
  .footer-email {
    font-size: clamp(16px, 5vw, 22px);
    font-weight: 400;
    text-align: center;
    word-break: break-word;
  }
  