.befhero-container {
    width: 100%;
    padding: 16px;
  }
  
  .befhero-title {
    font-size: clamp(20px, 6vw, 32px);
    letter-spacing: 2px;
    font-weight: 500;
    margin-bottom: 8px;
    word-break: break-word;
    text-align: left;
  }
  
  .befhero-description {
    font-size: clamp(14px, 4.5vw, 20px);
    color: rgb(172, 172, 172);
    letter-spacing: 1.5px;
    margin: 0;
    word-break: break-word;
    text-align: left;
  }
  