.hero-container {
    width: 100%;
    padding: 16px 16px 35px 16px;
  }
  
  .hero_content-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 24px;
  }
  
  .hero_poster-container {
    width: 100%;
    height: auto;
  }
  
  .hero-poster {
    width: 100%;
    height: auto;
    object-fit: cover;
  }
  
  .hero_text-content {
    width: 100%;
    font-size: clamp(16px, 5vw, 22px);
    font-weight: 300;
    text-align: left;
  }
  
  .hero_text-content p {
    margin-bottom: 12px;
    line-height: 1.4;
  }
  
  @media (min-width: 768px) {
    .hero_content-container {
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-start;
    }
  
    .hero_poster-container,
    .hero_text-content {
      max-width: 48%;
    }
  }
  