.credits-wrapper {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: auto;
  }
  
  .credits-container {
    transform: translateY(150%);
    opacity: 0;
    transition: transform 3.5s ease-out, opacity 2s ease-out;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 60px;
    padding-bottom: 60px;
    box-sizing: border-box;
  }
  
  .animated {
    transform: translateY(0%);
    opacity: 1;
  }
  
  .credits_item-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-bottom: 40px;
  }
  
  .credits_item-title {
    font-size: clamp(18px, 2vw, 26px);
    font-weight: 600;
    text-transform: uppercase;
    margin-bottom: 10px;
  }
  
  .credits_item-name {
    font-size: clamp(24px, 4vw, 42px);
    font-weight: 600;
    letter-spacing: 3px;
    text-transform: uppercase;
  }  